.textarea {
    width: 100%;
    height: 85%;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid $color-gray;
    background: none;
    font-family: $font-main-regular;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: $color-secondary;

    &::placeholder {
        color: $color-secondary;
    }
}