.cart-main {
    position: relative;
    overflow: hidden;

    &__image-cont {
        max-width: 100%;
        height: 280px;
        overflow: hidden;
        background-image: url("../img/Image_cart/Image_cart_cap.png");
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        display: inline-block;
        width: 100%;
        padding: 20px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        text-transform: uppercase;
        color: $color-secondary;
        text-decoration: none;
        border: 1px solid $color-light;

        &:hover {
            color: $color-primary;
            transition: all 600ms;
        }
    
        &:active {
            color: $color-primary;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            color: $color-primary;
        }
    }

    &__block-price {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 60px;
        border-left: 1px solid $color-light;
        border-right: 1px solid $color-light;
        border-bottom: 1px solid $color-light;
    }

    &__price {
        width: 100%;
        height: 60px;
        padding: 20px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        text-transform: uppercase;
        color: $color-secondary;
        border-right: 1px solid $color-light;
    }

    &__basket {
        position: relative;
        width: 100%;
        padding: 22px 15px 20px 15px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        text-decoration: none;
        color: $color-primary;

        &::after {
            content: "";
            position: absolute;
            top: 40%;
            right: 5%;
            background: url("../img/Icon/Icon _ arrow.svg") no-repeat;
            width: 20px;
            height: 15px;
            transition: all 600ms;
        }

        &:hover::after {
            background: url("../img/Icon/Icon _ arrow_white.svg") no-repeat;
            transition: all 600ms;
        }

        &:hover {
            background: $color-primary;
            text-decoration: underline;
            color: $white;
            transition: all 600ms;
            
        }
    
        &:active {
            color: $color-secondary;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            color: $color-secondary;
        }
    }

    &__caption {
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        background: $color-primary;
        padding: 20px 11px 19px 11px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $white;
    }
}