.table {
    max-width: 100%;
    border: none;
    border-collapse: collapse;
    background: $white;

    & td, th {
        padding: 16px 20px;
    }

    &__title {
        vertical-align: top;
        font-weight: bold;
        text-align: left;
        border: none;
        background: $color-secondary;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: $white;
    }

    &__subtitle {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: $color-secondary;
        vertical-align: top;
    }

    &__text {
        padding: 18px 20px;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: $black;
        vertical-align: top;
        text-align: left;
    }

    & tbody tr:nth-child(even){
        background: #EAE8E8;
    }
}

.wrap-table {
    max-width: 900px;
    overflow-x: auto;
}