.text-image {
    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__paragrafe {
        margin: 0 0 20px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }

    &__image {
        margin: 0 0 20px 0;
        height: auto;
        width: 100%;
    }
}

@media (min-width: $desktop) {
    .text-image {
        margin: 58px 0 0 0;
        &__grid-main {
            display: grid;
            grid-template: 1fr 1fr / 1fr;
            gap: 100px 0;
        }

        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 0 58px;

            &--extra {
                grid-template-columns: 7fr 4fr;
            }
        }

        &__col-1_2 {
            order: 1;
        }

        &__col-2_2 {
            order: 0;
        }
    
        &__paragrafe {
            margin: 0;
        }
    }
  }