.header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  height: auto;
  padding: 0 0;
  background: $white;
  box-shadow: 0px 4px 24px rgba(63, 63, 63, .05);
  
  &__grid {
      width: 100%;
      height: auto;
      display: grid;
      grid-template-columns: 1fr 90px;
      grid-template-rows: auto;
      align-items: center;
      justify-content: space-between;
  }

  &__col-2 {
    order: 1;
  }

  &__col-3 {
    order: 3;
    grid-column-start: 1;
    grid-column-end: 3;
  }

  &__col-5 {
    order: 2;
  }

  &__logotype {
    margin: 18px 20px;
  }

  &__catalog-btn {
    position: relative;
    z-index: 1;
    display: inline-block;
    width: 100%;
    padding: 24px 20px;
    background: $color-secondary;
    text-decoration: none;
    font-family: $font-main-bold;
    color: $white;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
    letter-spacing: .6px;
    text-transform: uppercase;
    transition: all 600ms;
    box-shadow: 0px 4px 24px rgba(63, 63, 63, .05);

    &:disabled {
      opacity: .55;
    }

    &--active {
      color: $color-gray;
    }
  }

  &__catalog-menu {
    position: absolute;
    display: none;
    top: 144px;
    left: 0;
    z-index: 2;
    width: 100%;
    min-height: 100%;
    margin: 0;
    padding: 20px;
    background: $color-secondary;
    box-shadow: 0px 25px 24px -30px rgba(0, 0, 0, .4) inset;
  }

  &__catalog-list {
    overflow-y: auto;
    overflow-x: auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, 125px) 125px;
    gap: 30px;
    width: 100%;
    max-height: 210px;
    margin: 0;
    padding: 0;
    list-style: none;

    &::-webkit-scrollbar {
      background-color: rgba(63, 63, 63, .0);
    }

    &::-webkit-scrollbar-track {
      background-color: rgba(63, 63, 63, .0);
    }

    &::-webkit-scrollbar-thumb {
      background: $color-primary;
    }
  }

  &__catalog-item {
    display: flex;
    flex-direction: column;
  }

  &__catalog-image {
    width: 100%;
    height: auto;
  }

  &__catalog-title {
    display: inline-block;
    margin: 10px auto 0 auto;
    font-family: $font-main-bold;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.6px;
    text-transform: uppercase;
    text-decoration: none;
    color: $white;

    &:disabled {
      opacity: .55;
    }

    &:focus {
      color: $color-primary;
      transition: all 600ms;
    }
  }

  &__block-icon {
    display: flex;
    padding: 15px 2px 15px 0;
  }

  &__block-basket {
    position: relative;
    width: 43px;
  }

  &__basket-title {
    position: absolute;
    top: 10px;
    right: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $color-primary;
    width: 14px;
    height: 14px;
    border-radius: 50%;

    font-family: $font-main-bold;
    font-style: normal;
    font-weight: bold;
    font-size: 8px;
    line-height: 9px;
    text-align: center;
    color: $white;
  }

  &__search-form {
    z-index: 1;
    position: absolute;
    bottom: 0;
    right: 0;
    display: none;
    width: 100%;
    height: 70px;
    color: $white;
  }

  &__search-form-grid {
    display: flex;
    text-align: center;
  }

  &__search-form-col {
    display: flex;
    align-items: flex-end;
    width: 100%;
    height: 70px;
    background: $color-primary;
    padding: 20px;
  }

  &__search-form-input {
    width: 89%;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid $white;
    font-family: $font-main-regular;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    padding: 5px 0 5px 0;
    background: $color-primary;
    color: $white;
  
    &::placeholder {
      color: $white;
    }
  }

  &__search {
    z-index: 3;
    position: relative;
    display: inline-block;
    width: 100%;
    height: 70px;
    padding: 13px 20px;
    text-align: center;
    background: $color-primary;
    box-shadow: 0px 4px 24px rgba(63, 63, 63, .05);
    border: none;
    cursor: pointer;
    outline: none;
    vertical-align: middle;
  }
}

@media (min-width: $tablet) {
  .header {
    padding: 0;
    background: $white;
    box-shadow: 0px 4px 24px rgba(63, 63, 63, .05);
  }
}

@media (min-width: $desktop) {
  .header {
    &__grid {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 43px 43px;
      width: $desktop;
      max-width: $desktop;
      margin: 0 auto;
    }

    &__col-2 {
      order: 0;
    }
  
    &__col-3 {
      order: 0;
      grid-column-start: inherit;
      grid-column-end: inherit;
    }
  
    &__col-5 {
      order: 0;
    }

    &__logotype {
      margin: 0;
    }

    &__catalog-btn {
      float: right;
      width: auto;
      padding: 10px 10px;
      border: 1px solid rgba(63, 63, 63, .1);
      background: $white;
      box-shadow: none;
      color: $color-secondary;

      &:hover {
        color: $white;
        background: $color-primary;
        transition: all 600ms;
      }

      &--active {
        color: $color-primary;
      }
    }

    &__catalog-menu {
      top: 73px;
      padding: 50px 20px 40px 20px;
      background: $white;
      box-shadow: 0px 25px 24px -30px rgba(63, 63, 63, 0.1) inset, 0px 25px 24px -30px rgba(63, 63, 63, 0.1);
    }
  
    &__catalog-title {
      color: $color-secondary;
      transition: all 600ms;

      &:hover {
        color: $color-primary;
        transition: all 600ms;
      }

      &:active {
        color: #954141;
        transition: all 600ms;
      }
    }

    &__hover {
      &:hover {
        background: $color-light;
        border-radius: 50%;
      }
    }

    &__search-form {
      width: 70%;
      right: 30px;
    }

    &__search-form-grid {
      display: flex;
      text-align: center;
      justify-content: flex-end;
    }

    &__search-form-col {
      background: $white;
      padding: 25px 20px 18px 20px;
      justify-content: flex-end;
    }

    &__search-form-input {
      width: 745px;
      margin: 0 30px 0 0;
      background: $white;
      border-bottom: 1px solid #CBCBCB;
      padding: 5px 0 5px 15px;
      color: $color-secondary;

      &::placeholder {
        color: $color-secondary;
      }
    }

    &__search {
      width: 40px;
      padding: 0;
      background: none;
      box-shadow: none;

      & path {
        stroke: $color-primary;
      }
    }
  }
}