.cart-portfolio {
    position: relative;
    overflow: hidden;

    &__image-cont {
        max-width: 100%;
        height: 168px;
        overflow: hidden;
        background-image: url("../img/Image_cart/Image_cart_cap.png");
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__block-title {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 60px;
        border: 1px solid $color-light;
        background: $white;
    }

    &__title {
        max-width: 164px;
        padding: 0px 20px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 22px;
        text-align: left;
        text-transform: uppercase;
        color: $color-secondary;
        background: $white;
    }

    &__date {
        width: 76px;
        padding: 21px 20px 20px 20px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 17px;
        text-align: center;
        text-transform: uppercase;
        text-decoration: none;
        color: $color-secondary;
        background: $white;
        border-left: 1px solid $color-light;
    }
}

@media (min-width: $desktop) {
    .cart-portfolio {
        &__image-cont {
            height: 335px;
        }

        &__block-title {
            z-index: 2;
            position: absolute;
            top: initial;
            bottom: 0;
            left: 0;
            justify-content: flex-start;
            align-items: flex-end;
            height: auto;
            border: none;
            background: initial;
        }
    
        &__title {
            max-width: 250px;
            padding: 10px 20px 9px 20px;
            border-left: 1px solid $color-light;
            border-top: 1px solid $color-light;
            border-bottom: 1px solid $color-light;
            font-size: 14px;
        }
    
        &__date {
            width: 77px;
            padding: 12px 20px;
            border-top: 1px solid $color-light;
            border-right: 1px solid $color-light;
            border-bottom: 1px solid $color-light;
            font-size: 14px;
        }
    }
}