.block-cart-category {
    padding: 50px 0;

    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px 20px;
        margin: 0 0 40px 0;
    }
}

@media (min-width: $tablet) {
    .block-cart-category {
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
    }
}

@media (min-width: $desktop) {
    .block-cart-category {
        padding: 100px 0;
    
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            margin: 0 0 50px 0;
        }
    }
}