.social {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin: 0;
    padding: 0;

    &__item {
        width: 40px;
        height: 40px;
        margin: 0 20px 0 0;
        border: 1px solid $white;
        color: $white;

        &:hover {
            background: $white;
            color: $color-secondary;
            transition: all 600ms;
        }
    }

    &__link {
        color: $white;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        text-decoration: none;

        &:hover {
            color: $color-secondary;
            transition: all 600ms;
        }
    
        &:active {
            color: $color-secondary;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            color: $color-primary;
        }
    }
}