.cart-price {
    &__grid {
        display: flex;
        flex-direction: row;
        margin: 0 0 20px 0;
    }

    &__block-image {
        overflow: hidden;
        width: 80px;
        height: 80px;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__block-text {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0 0 0 13px;
    }

    &__title {
        display: inline-block;
        margin: 0 0 10px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $color-secondary;
    }

    &__descriptor {
        margin: 5px 0 0 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 11px;
        line-height: 13px;
        letter-spacing: 0.81px;
        color: $color-secondary;
    }

    &__info-extra {
        display: inline-block;
        margin: 0 0 20px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $color-secondary;
        opacity: 0.55;
    }

    &__block-price {
        padding: 20px;
        border: 1px solid $color-light;
    }

    &__grid-extra {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 0 0 40px 0;
    }

    &__delivery-text {
        margin: 0 0 10px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 0.20625px;
        color: $color-secondary;
    }

    &__price {
        margin: 0 0 10px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 0.20625px;
        color: $color-secondary;
        text-align: right;
    }

    &__text-size {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 18px;
        color: $color-secondary;
        text-align: right;
    }

    &__caption {
        display: inline-block;
        margin: 10px 0 0 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $color-secondary;
        opacity: 0.55;
    }
}
