.block-catalog {
    &__grid-main {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px;
    }

    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px;
        margin: 0 0 40px 0;
    }
}

@media (min-width: $tablet) {
    .block-catalog {
        &__grid-main {
            display: grid;
            grid-template-columns: 1fr 2fr;
            gap: 20px;
        }

        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 20px;
            margin: 0 0 40px 0;
        }
    }
}

@media (min-width: $desktop) {
    .block-catalog {
        &__grid-main {
            display: grid;
            grid-template-columns: 1fr 3fr;
            gap: 20px;
        }

        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 20px;
            margin: 0 0 50px 0;
        }
    }
}