.radio-main {
    margin: 0;
    padding: 0;

    &__item {
        margin: 0 0 15px 0;
        list-style: none;
    
        &:last-child {
            margin: 0;
        }
    }

    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__caption {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        text-align: left;
        color: $color-secondary;
    }

    &__input {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 20px;
    
        &:disabled + .radio-main__text {
            opacity: 0.3;
        }
    
        &:disabled + .radio-main__text:hover::before {
            border: 1px solid $color-secondary;
            transition: all 600ms;
        }
    
        &:focus + .radio-main__text::before {
            border: 1px solid $color-secondary;
            transition: all 600ms;
        }
    }

    &__text {
        padding-left: 31px;
        position: relative;
        cursor: pointer;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        text-align: left;
        color: $color-secondary;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 16px;
            height: 16px;
            border: 1px solid $color-gray;
            border-radius: 50%;
            background: $white;
        }

        &::after {
            content: "";
            position: absolute;
            top: 5px;
            left: 5px;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: $color-primary;
            opacity: 0;
            transition: 0.2s;
        }
    }
}

@media (min-width: $desktop) {
    .radio-main__text:hover::before {
        border: 1px solid $color-secondary;
        transition: all 600ms;
    }
}

.radio-main__caption input:checked + .radio-main__text::after {
    opacity: 1;
}