/* BEGIN BREADCRUMBS */

.breadcrumbs {
  	padding-top: 20px;
	&--extra {
		margin: 0;
		padding-bottom: 40px;
	}
  	&__cont {
		display: block;
		height: 20px;
  		margin: 0;
  		padding: 0;
  		list-style: none; 
	} 

	&__list {
	  	float: left;
	}

	&__link {
		margin-right: 16px;
	  	font-family: $font-main-regular;
	  	font-size: 14px;
		line-height: 19px;
	  	font-weight: normal;
	  	font-style: normal;
	  	font-stretch: normal;
	  	letter-spacing: 0.2625px;
	  	text-decoration: none;
	  	color: $color-secondary;
		transition: all 600ms;
		border-bottom: 1px solid $white;

	  	&--active {
			display: inline-block;
			margin-right: 16px;
			font-family: $font-main-regular;
			font-size: 14px;
			line-height: 19px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			letter-spacing: 0.2625px;
			text-decoration: none;
			transition: all 600ms;
			border-bottom: 1px solid $white;
	  		color: $color-primary;
			transition: all 600ms;
	  	}

		&--active:hover {
			transition: all 600ms;
		}

	  	&:hover {
			color: $color-primary;
			border-bottom: 1px solid $color-primary;
			transition: all 600ms; 
		}
	}

	&__icon {
		margin-right: 16px;
	}

	&__line {
		display: inline-block;
		margin: 0px 8px 0px 5px;
	}
}

@media (min-width: $desktop) {
	.breadcrumbs {
		padding-top: 25px;
	}
}

/* END BREADCRUMBS */