.block-instagram {
    padding: 0 0 40px 0;

    &__block-text {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 650px;
        margin: 0 auto 20px auto;
    }

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 28px;
        line-height: 32px;
        text-align: center;
        color: $color-secondary;
    }

    &__subtitle {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        text-align: center;
        color: $color-secondary;
    }

    &__grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px 20px;
    }

    &__image {
        width: 100%;
        height: auto;
    }
}

@media (min-width: $tablet) {
    .block-instagram {
        &__grid {
            grid-template: repeat(2, 1fr) / repeat(6, 1fr);
        }
    }
}

@media (min-width: $desktop) {
    .block-instagram {
        &__grid {
            grid-template: repeat(2, 1fr) / repeat(6, 1fr);
        }

        &__col-1 {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        &__col-2 {
            grid-column: 2/4;
            grid-row: 1/3;
        }

        &__col-3 {
            grid-column: 4/5;
            grid-row: 1/2;
        }

        &__col-4 {
            grid-column: 5/7;
            grid-row: 1/3;
        }

        &__col-5 {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        &__col-6 {
            grid-column: 4/5;
            grid-row: 2/3;
        }
    }
}