.box-fill {
    padding: 40px 20px;
    background: $white;

    &--mb-40 {
        margin: 0 0 40px 0;
    }

    &__grid-main {
        display: grid;
        grid-template-columns: 1fr;
        gap: 40px 0;
    }

    &__title {
        margin: 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 21px;
        line-height: 28px;
        color: $color-secondary;
    }

    &__subtitle {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
        color: $color-secondary;

        &--extra {
            margin: 0;
        }
    }
}

@media (min-width: $desktop) {
    .box-fill {
        padding: 40px 40px;
        background: $white;
    
        &__grid-main {
            display: grid;
            grid-template-columns: 300px 1fr;
            gap: 0 92px;
        }

        &__title {
            font-size: 28px;
            line-height: 32px;
        }
    }
}