.arrow-bottom {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;

    &__title {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $color-light;
    }
}

@media (min-width: $tablet) {
    .arrow-bottom {
        display: flex;
        align-items: flex-start;
    }
}