.advantages {
    padding: 40px 0 0 0;

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 26px;
        line-height: 30px;
        color: $color-secondary;
    }

    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__cart {
        display: flex;
        flex-direction: row;
        margin: 0 0 20px 0;
    }

    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 80px;
        height: 80px;
        margin: 0 20px 0 0;
        background: $color-primary;
    }

    &__description-icon {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .advantages {
        padding: 40px 0 0 0;
    
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
    }
}

@media (min-width: $desktop) {
    .advantages {
        padding: 100px 0 132px 0;

        &__title {
            margin: 0 0 50px 0;
        }
    
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 10px 10px;
        }
    }
}