/* BEGIN BREADCRUMBS */
.breadcrumbs-extra {
    padding-top: 20px;
    margin: 0 0 80px 0;
    &__cont {
        display: block;
        height: 20px;
        margin: 0;
        padding: 0;
        list-style: none; 
    } 

    &__list {
        float: left;
        margin: 0 0 10px 0;
    }

  &__link {
        margin-right: 16px;
        font-family: $font-main-regular;
        font-size: 14px;
        line-height: 19px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        letter-spacing: 0.2625px;
        text-decoration: none;
        color: $white;
        transition: all 600ms;

        &--active {
            display: inline-block;
            margin-right: 16px;
            font-family: $font-main-regular;
            font-size: 14px;
            line-height: 19px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            letter-spacing: 0.2625px;
            text-decoration: none;
            transition: all 600ms;
            color: $color-gray;
            transition: all 600ms;
        }

      &--active:hover {
          transition: all 600ms;
      }

        &:hover {
          color: $white;
          transition: all 600ms; 
      }
  }

  &__icon {
      margin-right: 16px;
  }

  &__line {
      display: inline-block;
      margin: 0px 8px 0px 5px;
  }
}

@media (min-width: $desktop) {
  .breadcrumbs-extra {
      padding-top: 25px;
  }
}
/* END BREADCRUMBS */