.sim-slider {
    position: relative;
    width: 100%;
    height: 795px;
    background: $color-secondary;

    &__list {
        margin: 0;
        padding: 0;
        list-style-type: none;
        position: relative;
    }

    &__element {
        width: 100%;
        height: 795px;
        transition: opacity 1s ease-in;
        opacity: 0;
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        display: block;
    }

    &__image {
        width: 100%;
        height: 795px;
        background-repeat: no-repeat;
        object-fit: cover;
        opacity: .50;
    }

    &__text {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        height: 100%;
        padding: 15% 0 15% 0;
    }

    &__subtitle {
        display: inline-block;
        margin: 0 0 15px 0;
        font-family: $font-main-semibold;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px;
        letter-spacing: 0.6px;
        text-align: left;
        text-transform: uppercase;
        color: $color-light;
    }

    &__maintitle {
        margin: 0 0 3% 0;
        font-family: $font-main-bold;
        font-size: 32px;
        font-style: normal;
        font-weight: 800;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: left;
        color: $white;
    }

    &__paragrafe {
        margin: 0 0 5% 0;
        width: 100%;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $white;
    }

    &__btn-block {
        display: grid;
        grid-template-columns: 1fr; 
        gap: 20px 0; 
    }

    &__arrow-block {
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;
    }

    &__arrow-left,
    &__arrow-right {
        display: block;
        width: 40px;
        height: 40px;
        position: absolute;
        bottom: 20%;
        cursor: pointer;
        opacity: 1;
        z-index: 4;
        transition: all 600ms;
    }

    &__arrow-left {
        left: 0;
        background: url("../img/Icon/Icon _ arrow-left.svg") no-repeat;

        &:hover {
            opacity: .6;
            transition: all 600ms;
        }
    }

    &__arrow-right {
        right: 0;
        background: url("../img/Icon/Icon _ arrow-right.svg") no-repeat;

        &:hover {
            opacity: .6;
            transition: all 600ms;
        }
    }

    &__dots {
        position: absolute;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        right: 0;
        bottom: 21%;
        z-index: 3;
        width: 100%;
        height: initial;
        margin: 0;
        text-align: center;
    }
}
  
.sim-dot {
    display: inline-block;
    height: 4px;
    width: 4px;
    margin: 10px;
    background-color: $white;
    transition: background-color 0.6s ease;
    opacity: 0.55;
    cursor: pointer;
}

@media (min-width: $tablet) {
    .sim-slider {
        height: 80vh;

        &__element {
            height: 80vh;
        }
    
        &__image {
            height: 80vh;
        }

        &__grid {
            width: 80%;
            padding: 4% 0 4% 0;
        }

        &__btn-block {
            display: grid;
            grid-template-columns: 150px auto; 
            gap: 0px 20px; 
        }

        &__arrow-block {
            width: 100%;
            height: 100%;
            position: relative;
        } 

        &__arrow-left,
        &__arrow-right {
            top: 10%;
        }
        
        &__arrow-left {
            left: initial;
            right: 40px;
        }
    
        &__arrow-right {
            right: 0;
        }

        &__dots {
            flex-direction: column;
            width: initial;
            height: 100%;
            margin: 0 27px;
            bottom: 0;
        }
    }
}

@media (min-width: $desktop) {
    .sim-slider {
        height: 89.5vh;

        &__element {
            height: 89.5vh;
        }
    
        &__image {
            height: 89.5vh;
        }

        &__maintitle {
            margin: 0 0 5% 0;
            font-family: $font-main-bold;
            font-weight: 800;
            font-size: 64px;
            line-height: 64px;
            text-align: left;
            color: $white;
        }

        &__paragrafe {
            max-width: 611px;
        }
    }

    .sim-dot {
        margin: 17px 10px;
    }
}