.newsletter {
    padding: 40px 0;
    background: $color-primary;

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-semibold;
        font-style: normal;
        font-weight: 800;
        font-size: 26px;
        line-height: 30px;
        display: flex;
        align-items: center;
        text-align: center;
        flex-direction: column;
        color: $white;

        &::after {
            content: "";
            display: flex;
            justify-content: center;
            width: 60px;
            height: 1px;
            margin: 20px 0 0 0;
            background: $white;
        }
    }

    &__description {
        margin: 0 0 40px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        text-align: center;
        color: $white;
    }

    &__form {
        display: flex;
    }

    &__input {
        padding: 0;
        -webkit-box-shadow: inset 0 0 0 50px $color-primary;
        -webkit-text-fill-color: $white;
        width: 100%;
        margin: 0 0 15px 0;
        border-top: none;
        border-left: none;
        border-right: none;
        border-bottom: 1px solid $white;
        background: none;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: $white;

        &::placeholder {
            color: $white;
        }
    }

    &__btn {
        width: 27px;
        height: 27px;
        background: initial;
        border: none;

        &::after {
            content: url("../img/Icon/Icon _ arrow-right-small.svg");
            width: 27px;
            height: 27px;
        }
    }
}

@media (min-width: $desktop) {
    .newsletter {
        &__grid {
            display: grid;
            grid-template-columns: 242px 335px 1fr;
            gap: 0 40px;
        }

        &__title {
            margin: 0;
            align-items: left;
            text-align: left;
            border-right: 1px solid $white;
    
            &::after {
                content: "";
                display: none;
            }
        }
    
        &__description {
            margin: 0;
            text-align: left;
        }
    
        &__form {
            margin: 5px 0 0 0;
        }
    
        &__input {
            margin: 0 0 0 43px;
        }
    
        &__btn {
            width: 40px;
            height: 40px;
    
            &::after {
                content: url("../img/Icon/Icon _ arrow-right-big.svg");
                width: 40px;
                height: 40px;
            }
        }
    }
}