.section-title-main {
    margin: 0;
    padding: 20px 0 40px 0;
    display: block;
    align-items: center;

    &__text {
        margin: 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 38px;
        line-height: 48px;
        color: $color-secondary;
    }
}

@media (min-width: $desktop) {
    .section-title-main {
        padding: 30px 0 82px 0;
        
        &__text {
            font-size: 48px;
        }
    }
  }