.tabs {
    &__nav {
        overflow: hidden;
        margin: 0;
        padding: 0;

        & a {
            display: block;
            padding: 6px 0 6px 0;   
            font-family: $font-main-bold;
            font-style: normal;
            font-weight: bold;
            font-size: 10px;
            line-height: 18px;
            text-transform: uppercase;
            text-decoration: none;
            color: $color-gray;
            transition: all 600ms;
        }

        & a.active {
            position: relative;
            color: $color-secondary;
            transition: all 600ms;
        } 

        & a.active::after {
            content: "";
            position: absolute;
            width: 100%;
            left: 0;
            bottom: -1px;
            border-bottom: 1px solid $color-primary;
            transition: all 600ms; 
        } 
    }

    &__nav-item {
        display: block;
        padding: 0;
        list-style: none;
        border-bottom: 1px solid $color-gray; 
    }
    
    &__nav-item:last-child {
        padding: 0;
    }

    &__item {
        padding: 20px 0 0 0;

        &--extra {
            background: $color-light;
            margin: 40px 0 0 0;
            padding: 20px 20px 20px 20px;
        }
    }

    &__block {
        margin: 0 0 40px 0;
    }

    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 15px 0;
    }
}

@media (min-width: $tablet) {
    .tabs {
        &__nav-item {
            float: left;
            padding: 0 20px 0 0;
        }

        &__grid {
            display: grid;
            grid-template-columns: 55% 1fr;
            gap: 0 56px;

            &--extra {
                grid-template-columns: 1fr 1fr;
                gap: 0 7px;
            }
        }

        &__item {
            &--extra {
                margin: 30px 0 0 0;
                padding: 40px 40px 40px 40px;
            }
        }
    }
}