.result-search {
    margin: 0 0 20px 0;
    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__item {
        margin: 0 0 20px 0;
        padding: 20px;
        background: $color-light;
        border: 1px solid #CBCBCB;
        box-sizing: border-box;
    } 

    &__link {
        margin: 0 0 10px 0;
        padding: 0 0 4px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: $color-primary;
        text-decoration: none;
        border-bottom: 1px solid $color-primary;
    }

    &__description {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $desktop) {
    .result-search {
        margin: 0 0 30px 0;
    }
  }