/* --- BEGIN FONTS --- */

$font-main-regular: "Montserrat-Regular", "Roboto Condensed", "Scada", sans-serif;
$font-main-semibold: "Montserrat-SemiBold", "Roboto Condensed", "Scada", sans-serif;
$font-main-bold: "Montserrat-Bold", "Roboto Condensed", "Scada", sans-serif;
$font-main-extrabold: "Montserrat-ExtraBold", "Roboto Condensed", "Scada", sans-serif;

@font-face {
  font-family: "Montserrat-Regular";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat-SemiBold";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat-Bold";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat-ExtraBold";
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

/* --- END FONTS --- */