.block-questions {
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 40px 0;
    }

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 24px;
        line-height: 32px;
        color: $color-secondary;
    }

    &__subtitle {
        margin: 0 0 10px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $color-secondary;
    }

    &__paragrafe {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .block-questions {
        &__title {
            margin: 0 0 40px 0;
        }
        &__grid {
            gap: 0 20px;
        }
    }
  }

@media (min-width: $desktop) {
    .block-questions {
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
        }
        &__title {
            font-size: 32px;
            line-height: 40px;
        }
    }
  }