.block-nopage {
    height: 430px;
    padding: 0;
    background: url("../img/image_nopage.svg") no-repeat bottom 0 right 0;
    background-size: 100%;

    &__title {
        margin: 0 0 30px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 22px;
        line-height: 40px;
        color: $color-secondary;
    }

    &__paragrafe {
        margin: 0 0 40px 0;
        max-width: 100%;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .block-nopage {
        height: 1000px;
        padding: 20px 0 0 0;
    }
}

@media (min-width: $desktop) {
    .block-nopage {
        height: 925px;
        padding: 58px 0 0 0;
        background: url("../img/image_nopage.svg") no-repeat bottom 0 right 0;
        background-size: 100%;

        &__title {
            margin: 0 0 30px 0;
            font-size: 32px;
            line-height: 40px;
        }
    
        &__paragrafe {
            max-width: 558px;
        }
    }
}