.title-filter {
    display: inline-block;
    width: 100%;
    margin: 0 0 20px 0;
    transition: all 600ms;

    &__title {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        color: $color-secondary;
        text-decoration: none;
        transition: all 600ms;
        border-bottom: 1px solid $white;

        &:hover {
            border-bottom: 1px solid $color-secondary;
            transition: all 600ms;
        }
    
        &:active {
            color: $color-gray;
            transition: all 600ms;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            border-bottom: 1px solid $color-secondary;
            transition: all 600ms;
        }
    }

    &__icon {
        margin: 0 0 0 5px;
    }
}