.footer {
    background: $color-secondary;
    padding: 30px 0 20px 0;

    &__col-1, &__col-2, &__col-3, &__col-4 {
        margin: 0 0 40px 0;
    }

    &__col-4 {
        margin: 0 0 25px 0;
    }

    &__logotype {
        margin: 0 0 22px 0;
    }

    &__description {
        margin: 0 0 30px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $white;
    }

    &__list {
        list-style: none;
        padding: 0;
    }

    &__item {
        padding: 5px 0;
        &--title-extra {
            text-transform: uppercase;
            font-weight: bold;
        }
    }

    &__title-extra {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $white;
    }

    &__link {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 0.6px;
        text-decoration: none;
        color: #CBCBCB;

        &:hover {
            color: $color-primary;
            transition: all 600ms;
        }
    
        &:active {
            color: $color-primary;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            color: $color-primary;
        }
    }

    &__block-instagram {
        margin: 8px 0 0 0;
        display: grid;
        grid-template-columns: repeat( auto-fit, minmax(80px, 3fr) );
        gap: 15px 20px;
    }

    &__block-instagram-image {
        max-width: 100%;
    }

    &__copyright {
        border-top: 1px solid rgba(203, 203, 203, .1);
        padding: 8px 0 0 0;
    }

    &__copyright-text {
        margin: 0 0 18px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $white;
    }

    &__block-arrow {
        display: flex;
        justify-content: space-between;
    }

    &__arrow-top path {
        stroke: $color-primary;

        &:hover {
            stroke: $white;
        }
    }

    &__arrow-top {
        width: 100%;
        height: 100%;
        padding: 5px;
        &:hover path {
            stroke: $white;
        }
    }

    &__block-arrow-top {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        background: $white;
        transition: all 600ms;

        &:hover {
            background: $color-primary;
            transition: all 600ms;
        }

        &:active {
            background: $color-primary;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            background: $color-gray;
            transition: all 600ms;
        }
    }
}

@media (min-width: $tablet) {
    .footer {
        &__grid {
            display: grid;
            grid-template: 1fr 1fr / 3fr 3fr;
            gap: 20px;
        }

        &__col-1 {
            grid-column: 1/2;
        }

        &__col-2 {
            grid-column: 2/3;
        }

        &__col-3 {
            grid-column: 1/2;
        }

        &__col-4 {
            grid-column: 2/3;
        }

        &__list {
            margin: 0;
        }

        &__block-instagram {
            margin: 8px 0 0 0;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 15px 20px;
        }

        &__block-instagram-image {
            flex: none;
            margin-bottom: 15px;
        }
    }
}

@media (min-width: $desktop) {
    .footer {
        &__grid {
            display: grid;
            grid-template: 1fr / 3fr 3fr 3fr 3fr;
        }

        &__col-1 {
            grid-column: 1/2;
        }

        &__col-2 {
            grid-column: 2/3;
        }

        &__col-3 {
            grid-column: 3/4;
        }

        &__col-4 {
            grid-column: 4/5;
        }

        &__block-instagram {
            margin: 8px 0 0 0;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 15px 20px;
        }

        &__block-instagram-image {
            flex: none;
            margin-bottom: 15px;
        }

        &__copyright {
            padding: 20px 0 0 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        &__copyright-text {
            margin: 0;
        }
    
        &__block-arrow-top {
            margin: 0 0 0 30px;
        }
    }
}

.scroll-to {
    text-decoration: none;
}