.block-delivery-clearance {
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px 0;
    }

    &__col-1 {
        padding: 40px 20px;
        background: $color-light;

        &--extra {
            padding: 40px 40px;
        }
    }

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        text-transform: uppercase;
        color: $color-secondary;
    }

    &__paragrafe {
        margin: 0 0 40px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }

    &__caption {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.2625px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .block-delivery-clearance {
        &__grid {
            display: grid;
            grid-template-columns: 7fr 3fr;
            gap: 0 20px;
        }
    
        &__col-1 {
            padding: 40px;
            background: $color-light;
        }
    }
}
  
@media (min-width: $desktop) {
    .block-delivery-clearance {
        &__grid {
            display: grid;
            grid-template-columns: 7fr 3fr 2fr;
            gap: 0 20px;
        }
    
        &__col-1 {
            padding: 40px;
            background: $color-light;
        }

        &__title {
            margin: 0 0 15px 0;
        }
    
        &__paragrafe {
            margin: 0 0 25px 0;
        }
    }
}