.list {
    list-style: none;
    margin: 0;
    padding: 0;

    &--mb-40 {
        margin: 0 0 40px 0;
    }

    &__item-no-doted {
        margin: 0 0 20px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }

    &__item {
        margin: 0 0 20px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;

        &::before {
            content: "";
            background: $color-primary;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            display: inline-block;
            line-height: 2rem;
            color: $white;
            text-align: center;
            margin-bottom: 3px;
            margin-right: 0.5rem;
        }
    }
}
  