.popup {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 90%;
    height: 50%;
    background: $black;
    transform: translate(-50%, -50%) scale(0); // Центрируем и масштабируем в 0 само окно
    transition: 0.5s all;

    &__bg {
        z-index: 11;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.5);
        opacity: 0; 
        pointer-events: none; 
        transition: 0.5s all;

        &.active { 
            opacity: 1; 
            pointer-events: all; 
            transition: 0.5s all;
        }
    }

    &.active { // При добавлении класса 'active'
        transform: translate(-50%, -50%) scale(1); // Так же центрируем и плавно увеличиваем
        transition: 0.5s all;
    }

    &__close {
        position: absolute;
        top: 20px;
        right: 22px;
        cursor: pointer;
    }
}

@media (min-width: $tablet) {
    .popup {
        width: 90%;
        height: 80%;
    }
}

@media (min-width: $desktop) {
    .popup {
        width: 90%;
        height: 90%;
    }
}

.block-video {
    position: relative;
    height: 420px;
    overflow: hidden;
    background: $color-secondary;
    background-repeat: no-repeat;
    background-size: cover;

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: .2;
    }

    &__block-play {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: all 600ms;

        &:hover {
            opacity: .8;
            transform: translateY(-10px);
            transition: all 600ms;
        }
    }

    &__icon {
        width: 56px;
        height: 56px;
    }

    &__title {
        max-width: 300px;
        margin: 30px 0 0 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 26px;
        text-align: center;
        color: $white;
    }
}

@media (min-width: $desktop) {
    .block-video {
        &__icon {
            width: 80px;
            height: 80px;
        }
    }
}