.block-portfolio {
    margin: 0 0 20px 0;

    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px 0;
    }
}

@media (min-width: $tablet) {
    .block-portfolio {
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 20px 20px;
        }
    }
}

@media (min-width: $desktop) {
    .block-portfolio {
        margin: 0 0 50px 0;

        &__grid {
            gap: 0;
        }
    }
}