.section-title {
    margin: 0 0 20px 0;
    padding: 20px 0px;
    height: 113px;
    background: $color-light;

    &__text {
        display: flex;
        align-items: center;
        margin: 0;
        height: 100%;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 38px;
        line-height: 48px;
        color: $color-secondary;
    }
}

@media (min-width: $desktop) {
    .section-title {
        margin: 0 0 50px 0;
        height: 171px;
        
        &__text {
            font-size: 48px;
        }
    }
  }