.cart-best-seller {
    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__block-image {
        overflow: hidden;
        height: auto;
        margin: 0 0 -5px 0;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__block-text {
        padding: 20px;
        border: 1px solid $color-light;
    }

    &__caption {
        display: inline-block;
        margin: 0 0 5px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $color-secondary;
    }

    &__title {
        margin: 0 0 24px 0;
        font-family: $font-main-bold;
        font-style: bold;
        font-weight: 800;
        font-size: 26px;
        line-height: 30px;
        color: $color-secondary;
    }

    &__paragrafe {
        margin: 0 0 30px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .cart-best-seller {
        &__grid {
            display: grid;
            grid-template-columns: 49% 1fr;
        }

        &__block-image {
            margin: 0;
        }

        &__block-text {
            padding: 20px;
        }
    }
}

@media (min-width: $desktop) {
    .cart-best-seller {
        &__block-text {
            padding: 40px 65px;
        }
    }
}