/* begin checkbox-main */

.checkbox-main {
    padding: 0;
    margin: 0;

    &__item {
        margin: 0 0 15px 0;
        padding: 0;
        list-style: none;
    
        &:last-child {
            margin: 0;
        }
    }

    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__caption {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        text-align: left;
        color: $color-secondary;

        & input:checked + .checkbox-main__text::after {
            background: url("../img/Icon/Icon _ thick-check.svg") no-repeat top 7px left 5px;
        }
    }

    &__input {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 20px;

        &:disabled + .checkbox-main__text {
            opacity: 0.3;
        }

        &:disabled + .checkbox-main__text:hover::before {
            outline: 1px solid $color-secondary;
            transition: all 600ms;
        }

        &:focus + .checkbox-main__text::before {
            outline: 1px solid $color-secondary;
            transition: all 600ms;
        }
    }

    &__text {
        padding-left: 31px;
        position: relative;
        cursor: pointer;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        text-align: left;
        color: $color-secondary;
    
        &::before {
            content: "";
            position: absolute;
            top: 1px;
            left: 0;
            width: 16px;
            height: 16px;
            background: $white;
            outline: 1px solid $color-gray;
            transition: 0.2s;
        }
    
        &::after {
            content: "";
            position: absolute;
            top: -2px;
            left: -2px;
            width: 30px;
            height: 30px;
            transition: 0.2s;
        }
    }
}
  
@media (min-width: $desktop) {
    .checkbox-main__text:hover::before {
        outline: 1px solid $color-secondary;
        transition: all 600ms;
    }
}
  
/* end checkbox-main */
  