/* BEGIN SORTING */
.sorting {
    display: grid;
    justify-content: end;
    border: 0 none; 
    padding: 0;

    &__select {
        width: 100%;
        position: relative;
        display: inline-block;
        padding: 0;
        margin: 0; 
        padding-right: 0px;
        -webkit-appearance: none;
        -moz-appearance: none;
    }

    &__form {
        height: 19px;
        margin: 0 0 20px 0;
        padding: 0 22px 0 0;
        border: none;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 2.35846px;
        background: $white; 
        text-transform: uppercase;
        appearance: none;
        overflow: hidden;
        background: url('../img/Icon/Icon _ arrow-fill.svg') no-repeat right 0 top 4px;

        &:disabled {
            opacity: .30;
        }
    }
}
  
  /* END SORTING */