.nav-menu {
    &__menuToggle {  
        display: block;
        cursor: pointer;
    }

    &__icon {
        display: flex;
        align-items: center;
        cursor: pointer;
    }

    &__hamburger {
        display: inline-block;
        margin: 8px 12px;
        width: 19px;
        height: 2px;
        border-radius: 1px;
        background: $color-primary;
        transform: translate(0, -50%);
        transition: 0.5s;
    }

    &__hamburger:before,
    &__hamburger:after {
        content: "";
        position: absolute;
        width: 19px;
        height: 2px;
        border-radius: 1px;
        background: $color-primary;
        transition: 0.5s;
    }

    &__hamburger:before {
        top: -5px;
    }

    &__hamburger:after {
        top: 5px;
    }

    &__icon.active &__hamburger {
        background: rgba(238, 67, 67, 0);
    }

    &__icon.active &__hamburger:before {
        top: 0;
        transform: rotate(45deg);
    }
    
    &__icon.active &__hamburger:after {
        top: 0;
        transform: rotate(135deg);
    }
}

@media (min-width: $desktop) {
    .nav-menu {
        &__menuToggle {  
            display: none;
        }
    }

    &__icon {
        display: none;
    }
}