#map{
  width: 100%;
  height: 285px;
}

@media (min-width: $tablet) {
	#map{
		width: 100%;
		height: 515px;
	  }
}

.ymaps-layers-pane {
	filter: grayscale(0.9) hue-rotate(180deg);
}

.ymaps-b-balloon__sprite {
	display: none!important;
}

.ymaps-b-balloon__tail {
    background-image: initial!important;
}

.ymaps-b-balloon {
	position: relative;
	width: auto!important;
	height: auto!important;
	background: #EE4343;
	box-shadow: 0px 12px 22px rgba(0, 0, 0, 0.24);
	border-radius: 3px;
}

.ymaps-b-balloon::before {
	content: url("../img/Triangle.svg");
	position: absolute;
	bottom: -12px;
	right: 0;
	display: flex;
    width: 100%;
    z-index: -10;
    justify-content: center;
}

.ymaps-b-balloon__content {
    border: solid 1px #EE4343!important;
    background: initial!important;
}

.ymaps-b-balloon__content-holster {
    padding: 4px 78px 36px 78px!important;
}

.ymaps-b-balloon__content h4 {
	font-family: $font-main-bold;
    font-weight: bold;
	font-size: 12px;
	line-height: 15px;
	text-transform: uppercase;
	text-align: center;
}

.ymaps-b-balloon__content p {
	font-family: $font-main-regular;
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 15px;
	text-align: center;
}

.ymaps-b-balloon__content-body {
	text-align: center;
}

.ymaps-b-balloon__content-body, .ymaps-b-poi-balloon-content__title {
	font-family: $font-main-regular;
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 15px;
	text-align: left;
	color: $white;
}