.block-cart {
    padding: 50px 0px;

    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px 0;
    }

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 28px;
        line-height: 32px;
        text-align: center;
        color: $color-secondary;
    }

    &__subtitle {
        margin: 0 0 40px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        text-align: center;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .block-cart {
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 20px 20px;
            align-self: stretch;
        }
    }
}

@media (min-width: $desktop) {
    .block-cart {
        padding: 100px 20px;

        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 20px 20px;
        }

        &__block-text {
            max-width: 650px;
            margin: 0 auto;
        }

        &__title {
            font-size: 48px;
            line-height: 48px;
        }
    
        &__subtitle {
            margin: 0 0 50px 0;
        }
    }
}