.block-delivery {
    height: 430px;
    padding: 0;
    background: url("../img/image_delivery.svg") no-repeat bottom 0 right 0;
    background-size: 100%;
}

@media (min-width: $tablet) {
    .block-delivery {
        height: 1000px;
        padding: 20px 0 0 0;
    }
}

@media (min-width: $desktop) {
    .block-delivery {
        height: 925px;
        padding: 58px 0 0 0;
        background: url("../img/image_delivery.svg") no-repeat bottom 0 right 0;
        background-size: 100%;
    }
}