.cart-category {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    height: 188px;
    background-image: url('../img/Category_image_bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    text-decoration: none;
    transition: all 600ms;

    &:hover {
        transition: all 600ms;
        opacity: .8;
        transform: translateY(-10px);
    }

    &__title {
        max-width: 140px;
        padding: 5px 0 5px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $color-secondary;
        background: $white;
        transition: all 600ms;

        &:hover {
            color: $color-primary;
            transition: all 600ms;
        }
    
        &:active {
            color: $color-primary;
            transition: all 600ms;
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            color: $color-primary;
            transition: all 600ms;
        }
    }
}