.signature {
    max-width: 100%;
    margin: 0;
    padding: 15px 0 0 0;
    border-top: 1px solid $color-primary;
    font-family: $font-main-bold;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 28px;
    text-align: left;
    color: $color-secondary;
}

@media (min-width: $desktop) {
    .signature {
        max-width: 380px;
        margin: 0 auto;
        text-align: center;
    }
  }