.nav {
    position: absolute;
    display: none;
    top: 124px;
    left: 0;
    z-index: 0;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin: 20px 0;
    padding: 30px 0;
    background: $white;
    box-shadow: 0px 25px 24px -30px rgba(0, 0, 0, .4) inset;
  
    &__item {
        padding: 10px 14px;
        text-align: center;
        list-style: none;
    }

    &__link {
        text-decoration: none;
        font-family: $font-main-semibold;
        color: $color-secondary;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
        text-align: right;
        letter-spacing: .6px;
        text-transform: uppercase;
        transition: all 600ms;

        &:active {
            color: #954141;
            transition: all 600ms;
        }

        &:disabled {
            opacity: .55;
        }

        &:focus {
            color: $color-primary;
            transition: all 600ms;
        }

        &--active {
            color: $color-primary;
            transition: all 600ms;
        }
    }
}

@media (min-width: $desktop) {
    .nav {
        position: relative;
        top: initial;
        left: initial;
        display: flex;
        height: auto;
        margin: 0 40px 0 0;
        padding: 0;
        flex-direction: row;
        align-items: center;
        background: $white;
        box-shadow: none;
    
        &__menu {
            display: none;
        }
    
        &__item {
            flex: 0 0 auto;
            width: auto;
            padding: 0 14px;
            list-style: none;
        }
    
        &__link {
            border-bottom: 1px solid $white;
            &:hover {
                color: $color-primary;
                transition: all 600ms;
            }
    
            &:focus {
                color: $color-primary;
                transition: all 600ms;
            }
        }
    }
}
