.accordion {
    width: 100%;
    border-bottom: 1px solid $color-gray;

    &__body {
        display: none;
        padding: 14px 20px;
        margin: 8px 0;

        &--extra {
            max-width: 920px;
        }
    }

    &__body > * > .accordion__body {
        margin: 0;
    }

    &__header {
        color: $color-secondary;
        margin: 0;
        padding: 11px 20px 11px 14px;
        cursor: pointer;
        position: relative;
        border-top: 1px solid $color-gray;
        font-size: 18px;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: $color-secondary;

        &::after {
            content: url("../img/Icon/Icon _ Plus.svg");
            position: absolute;
            top: 0;
            right: 16px;
            font-family: $font-main-bold;
            font-size: 28px;
            line-height: 28px;
            font-weight: bold;
            color: $color-gray;
            
        }

        &.open {
            color: $color-secondary;
        }

        &.open::after {
            content: url("../img/Icon/Icon _ Minus.svg");
            color: $color-secondary;    
        }
    }

    &__grid-two-col {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px 0;
    }

    &__subtitle {
        margin: 0 0 10px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
        color: $color-secondary;

        &--extra {
            margin: 0;
        }
    }

    &__paragrafe {
        margin: 0 0 40px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .accordion {
        &__subtitle {
            margin: 0 0 30px 0;
        }

        &__paragrafe {
            margin: 0 0 30px 0;
        }
    }
}

@media (min-width: $desktop) {
    .accordion {
        &__grid-two-col {
            grid-template-columns: 1fr 1fr;
            gap: 0 20px;

            &--extra {
                grid-template-columns: 146px 1fr;
                gap: 0 35px;
            }
        }
    }
}