.counter {
    display: flex;
    flex-direction: row;
    justify-content: center;

    &__arrow {
        cursor: pointer;
    }

    &__input, &__arrow {
        margin: 0 5px 0 0;
        width: 30px;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: $color-secondary;
        border-top: 0;
        border-right: 0;
        border-bottom: 1px solid $color-gray;
        border-left: 0;
    }
  }

  @media (min-width: $desktop) {
    .counter {
        justify-content: left;
        
        &__input, &__arrow {
            padding: 5px 0 5px 0;
            height: 30px;
        }
    }
}