.cart-recommendations {
    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__block-image {
        overflow: hidden;
        width: 100%;
        height: 150px;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__block-text {
        padding: 20px;
        border: 1px solid $color-light;
    }

    &__title {
        display: inline-block;
        margin: 0 0 10px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 10px;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        color: $color-secondary;
    }

    &__paragrafe {
        margin: 0 0 17px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .cart-recommendations {
        &__grid {
            display: grid;
            grid-template-columns: 152px 1fr;
        }

        &__block-image {
            margin: 0;
            width: 152px;
        }

        &__paragrafe {
            margin: 0 0 31px 0;
            font-family: $font-main-regular;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            color: $color-secondary;
        }
    }
}
