$mobile: 320px;
$tablet: 768px;
$desktop: 1180px;

html {
  scroll-behavior: smooth;
}

body {
  min-width: 320px;
  margin: 0;
}

main {
  padding: 144px 0 40px 0;
}

@media (min-width: $desktop) {
  main {
    padding: 74px 0 150px 0;
  }
}

.main--npb {
  padding-bottom: 0;
}

.container {
  width: 90%;
  margin: 0 auto;
  height: 100%;
}

@media (min-width: $tablet) {
  .container {
    max-width: $tablet;
    margin: 0 auto;
    height: 100%;
  }
}

@media (min-width: $desktop) {
  .container {
    max-width: $desktop;
    margin: 0 auto;
    height: 100%;
  }
}

.mt-80 {
  margin: 40px 0 0 0;
}

@media (min-width: $tablet) {
  .mt-80 {
    margin: 80px 0 0 0;
  }
}

.mb-100 {
  margin: 0 0 40px 0;
}

@media (min-width: $tablet) {
  .mb-100 {
    margin: 0 0 100px 0;
  }
}

.mt-100 {
  margin: 40px 0 0 0;
}

@media (min-width: $tablet) {
  .mt-100 {
    margin: 100px 0 0 0;
  }
}

/* --- BEGIN GLOBAL COLORS --- */

$color-primary: #EE4343;
$color-primary-extra: #954141;

$color-secondary: #3F3F3F;

$color-dark: #0F0F0F;
$color-gray: #D8D5D5;
$color-light: #F8F4F4;

$white: #ffffff;
$black: #000000;

/* semantic color */

$error: #ff8282;
$success: #48D2A0;
$warning: #F8C51C;

/* --- END GLOBAL COLORS --- */

