.btn-icon {
    display: inline-block;
    padding: 15px 22px;
    background: $color-primary;
    border-radius: 25px;
    font-family: $font-main-regular;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 2.35846px;
    text-transform: uppercase;
    text-decoration: none;
    color: $white;
    transition: all 600ms;
    cursor: pointer;

    &_150 {
        width: 100%;
    }

    &_222 {
        width: 100%;
    }

    &_300 {
        width: 100%;
    }

    &--small {
        width: 100%;
        padding: 8px 20px;
    }

    &::after {
        content: "";
        background: url("../img/Icon/Icon _ basket_white.svg") no-repeat;
        display: inline-block;
        width: 13px;
        height: 12px;
        transition: all 600ms;
    }

    &:active::after {
        content: "";
        background: url("../img/Icon/Icon _ thick-check_white.svg") no-repeat;
        display: inline-block;
        width: 13px;
        height: 12px;
        transition: all 600ms;
    }

    &:hover {
        box-shadow: 0px 11px 24px rgba(238, 67, 67, 0.25);
        transition: all 600ms;
    }

    &:active {
        background-color: $color-primary-extra;
    }

    &:disabled {
        opacity: .55;
    }

    &:focus {
        box-shadow: 0px 11px 24px rgba(238, 67, 67, 0.25);
        transition: all 600ms;
    }

    &--secondary {
        background: $color-secondary;

        &:hover {
            box-shadow: 0px 11px 24px rgba(63, 63, 63, 0.25);
            transition: all 600ms;
        }
    
        &:active {
            background: $color-secondary;
            color: rgba($color: $white, $alpha: .55);
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            box-shadow: 0px 11px 24px rgba(63, 63, 63, 0.25);
            transition: all 600ms;
        }
    }

    &--gray {
        background: $color-light;
        color: $color-secondary;

        &:hover {
            background: #CBCBCB;
            box-shadow: 0px 11px 24px rgba(63, 63, 63, 0.04);
            transition: all 600ms;
        }
    
        &:active {
            background-color: $color-light;
            color: rgba($color: $color-secondary, $alpha: .55);
        }
    
        &:disabled {
            opacity: .55;
        }
    
        &:focus {
            box-shadow: 0px 11px 24px rgba(63, 63, 63, 0.04);
            transition: all 600ms;
        }
    }
}

@media (min-width: $tablet) {
    .btn-icon {   
        &_150 {
            width: 150px;
        }
        
        &_222 {
            width: 222px;
        }

        &_300 {
            width: 300px;
        }

        &--small {
            width: auto;
            padding: 8px 20px;
        }
    }
}