.block-contacts {
    &__grid {
        display: flex;
        flex-direction: column;
    }

    &__col-1 {
        width: 90%;
        margin: 0 auto;
    }

    &__title {
        margin: 0 0 30px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 22px;
        line-height: 40px;
        color: $color-secondary;
    }

    &__subtitle {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }

    &__address {
        margin: 0 0 30px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .block-contacts {
        background: linear-gradient(90deg, rgba(255,255,255,1) 50%, rgba(63,63,63,1) 50%);
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            max-width: 768px;
            margin: -20px auto 0 auto;
        }

        &__col-1 {
            width: auto;
            margin: initial;
            padding: 40px 20px 20px 0;
        }
    }
}

@media (min-width: $desktop) {
    .block-contacts {
        
        &__grid {
            max-width: 1180px;
            margin: 0 auto;
            margin: -50px auto 0 auto;
        }

        &__col-1 {
            padding: 80px 20px 80px 0;
        }
    }
}