.block-recommendations {
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 40px 0;
    }

    &__title {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 24px;
        line-height: 32px;
        color: $color-secondary;
    }
}

@media (min-width: $tablet) {
    .block-recommendations {
        &__title {
            margin: 0 0 40px 0;
        }
        &__grid {
            gap: 20px 0;
        }
    }
  }

@media (min-width: $desktop) {
    .block-recommendations {
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 0 20px;
        }
        &__title {
            font-size: 32px;
            line-height: 40px;
        }
    }
}