.info-cart {
    height: auto;
    margin: 0 0 40px 0;
    background: linear-gradient(to top, rgba(63, 63, 63, .55), rgba(63, 63, 63, .55)),
    url("../img/Bg-cart-product.png");
    background-size: cover;

    &__grid {
        display: block;
    }

    &__info {
        min-height: 100%;
        padding: 20px 0 20px 0;
        width: 100%;
    } 

    &__main-title {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 28px;
        line-height: 32px;
        color: $white;
    }

    &__description {
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $white;
    }

    &__price-grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 40px 0 0 0;
        padding: 0 0 40px 0;
        border-bottom: 1px solid $color-gray;
    }

    &__price-block {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__price-package {
        margin: 0 0 7px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 11px;
        line-height: 13px;
        letter-spacing: 0.81px;
        color: $white;
    }

    &__price-size {
        margin: 0 0 20px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 22px;
        text-transform: uppercase;
        color: $white;
    }

    &__color-block {
        margin: 40px 0 40px 0;
    }

    &__color-title {
        margin: 0 0 9px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 26px;
        color: $white;
    }

    &__color-subtitle {
        display: inline-block;
        margin: 0 0 27px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $white;
    }

    &__color-grid {
        display: block;
    }

    &__color-col {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 0 0 19px 0;
    }

    &__color-item {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 11px;
        line-height: 13px;
        letter-spacing: 0.81px;
        color: $white;
    }

    &__color-circle {
        display: inline-block;
        margin: 0 10px 0 0;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        box-shadow: 0px 11px 24px rgba(63, 63, 63, 0.04);

        &--active {
            border: 2px solid $white;
            border-radius: 50%;
        }
    }
}

@media (min-width: $tablet) {
    .info-cart {
        &__price-grid {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        &__price-block {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &__color-grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 20px 20px;
        }

        &__color-col {
            margin: 0;
        }
    }
}

@media (min-width: $desktop) {
    .info-cart {
        margin: 0 0 100px 0;
        background: url("../img/Bg-cart-product.png");

        &__grid {
            display: flex;
            justify-content: flex-end;
        }

        &__info {
            width: 60.6%;
            padding: 0 40px 76px 40px;
            background: linear-gradient(to top, rgba(63, 63, 63, .55), rgba(63, 63, 63, .55));
        } 
        
        &__price-grid {
            padding: 106px 0 20px 0;
        }
    }
  }