.basket {
    margin: 0 0 40px 0;
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
    }

    &__col-2 {
        padding: 20px 0 40px 0;
        text-align: center;
        border: 1px solid $color-gray;
    }

    &__block-image {
        overflow: hidden;
        width: 100%;
        height: 150px;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__subtitle {
        margin: 0 0 10px 0;
        display: block;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 0.20625px;
        color: $color-secondary;
        opacity: 0.55;

        &--extra {
            display: block;
            margin: 40px 0 10px 0;
        }
    }

    &__title {
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 20px;
        text-transform: uppercase;
        color: $color-secondary;
    }

    &__description {
        display: grid;
        grid-template-columns: 1fr;
        gap: 5px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 11px;
        line-height: 13px;
        letter-spacing: 0.81px;
        color: $color-secondary;

        &::after {
            content: "";
            width: 60px;
            border: 1px solid $color-gray;
            margin: 20px auto 40px auto;
        }
    }

    &__price {
        display: flex;
        flex-direction: column;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 18px;
        letter-spacing: 0.81px;
        color: $color-secondary;

        &::after {
            content: "";
            width: 60px;
            border: 1px solid $color-gray;
            margin: 20px auto 40px auto;
        }
    }

    &__result {
        display: flex;
        flex-direction: column;
        padding: 20px 40px;
        text-align: center;
        background: $color-light;
    }

    &__result-price {
        margin: 0 0 3px 0;
        font-family: $font-main-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 18px;
        text-align: center;
        color: $color-secondary;
    }

    &__result-clarification {
        margin: 0 0 10px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 10px;
        line-height: 12px;
        text-align: center;
        letter-spacing: 0.20625px;
        color: $color-secondary;
        opacity: 0.55;
    }

    &__result-text {
        margin: 0 0 20px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        color: $color-secondary;
    }
}
  
  @media (min-width: $desktop) {
    .basket {
        margin: 0 0 100px 0;
        &__grid {
            display: grid;
            grid-template-columns: 200px 2fr;
        }

        &__grid-extra {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            margin: 40px 40px 46px 40px;
        }

        &__col {
            width: 100%;
        }

        &__col-2 {
            display: grid;
            grid-template-columns: 400px 1fr;
            padding: 0 0 64px 0;
            text-align: left;
            border: none;
        }

        &__block-image {
            width: 200px;
            height: 200px;
            margin: 23px 0 0 0;
            border-top: 1px solid $color-gray;
        }
    
        &__subtitle {
            width: 100%;
            padding: 0 0 13px 40px;
            border-bottom: 1px solid $color-gray;
    
            &--extra {
                display: none;
                margin: 0;
            }
        }
    
        &__title {
            margin: 40px 40px 46px 40px;
            font-size: 24px;
            line-height: 20px;
        }
    
        &__description {
            margin: 40px 40px 0 40px;

            &::after {
                content: "";
                display: none;
            }
        }
    
        &__price {
            display: flex;
            flex-direction: row;
            margin: 0 40px 0 10px;
    
            &::after {
                content: "";
                display: none;
            }
        }
    
        &__result {
            justify-content: flex-end;
            margin: -4px 0 0 0;
            padding: 20px 80px 20px 302px;
            flex-direction: row;
            border-top: 1px solid $color-gray;
        }

        &__result-price {
            margin: 0 0 3px 0;
            text-align: left;
        }
    
        &__result-clarification {
            margin: 0 0 10px 0;
            text-align: left;
        }
    
        &__result-text {
            width: 217px;
            margin: 0 41px 0 120px;
            text-align: left;
        }
    }
}