.label {
    width: 100%;
    margin: 0 0 15px 0;
    padding: 0 0 10px 0;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid $color-gray;
    background: none;
    font-family: $font-main-regular;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: $color-secondary;

    &:active {
        border-bottom: 1px solid $color-primary;
    }

    &:focus {
        border-bottom: 1px solid $color-primary;
    }

    &:disabled {
        opacity: 35.95;
    }

    &:required:valid {
        border-color: $success;
    }

    &::placeholder {
        color: $color-secondary;
    }


}