.form {
    background: $color-secondary;
    padding: 30px 5% 20px 5%;

    &__input, &__textarea {
        width: 100%;
        margin: 0 0 15px 0;
        border-top: none;
        border-left: none;
        border-right: none;
        border-bottom: 1px solid $white;
        background: none;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: $white;

        &::placeholder {
            color: $white;
        }
    }

    &__input {
        padding: 0 0 10px 0;
        -webkit-box-shadow: inset 0 0 0 50px $color-secondary;
        -webkit-text-fill-color: $white;
    }

    &__textarea {
        height: 113px;
    }

    &__agreement {
        display: inline-block;
        margin: 0 0 30px 0;
        font-family: $font-main-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $color-light;
        opacity: 0.55;
    }

    &__btn {
        border: none;
    }
}

@media (min-width: $tablet) {
    .form {
        height: 100%;
        padding: 50px 10% 20px 10%;
    }
}

@media (min-width: $desktop) {
    .form {
        position: relative;
        padding: 103px 5% 20px 100px;
    }
}