/* BEGIN PAGINATE */
.paginate {
  	text-align: left;
  	width: 100%;

	&__list {
	  	display: flex;
		align-items: center;
		justify-content: center;
		margin: 0;
	  	padding: 0;
	  	list-style: none;
	  	text-align: left;

		&--left {
			justify-content: start;
		}
	}

	&__item {
	  	display: flex; 
		align-items: center;
		justify-content: center;
	  	margin-right: 10px;
		width: 35px;
	  	height: 35px;
		border-radius: 50%;
		background: $color-primary;

		font-family: $font-main-regular;
		font-size: 15px;
	  	font-weight: normal;
	  	font-style: normal;
	  	font-stretch: normal;
	  	line-height: 1.33;
	  	letter-spacing: normal;
	  	text-align: center;
	  	color: $white;
	  	text-decoration: none;
	  	transition: all .5s ease-out; 

	  	&:last-child {
	  		margin-right: 0px;
	  	}

		&:hover {
			box-shadow: 0px 11px 24px rgba(238, 67, 67, 0.25);
		}
	
		&:active {
			background-color: $color-primary-extra;
			color: rgba($color: $white, $alpha: .55);
		}
	
		&:disabled {
			opacity: .55;
		}
	
		&:focus {
			background: $color-primary-extra;
			box-shadow: 0px 11px 24px rgba(238, 67, 67, 0.25);
		}

		&--active {
			background: $color-primary-extra;
			color: $white;
			transition: all .5s ease-out;
		}
	}

	&__decor {
		display: none;
	}

	&__text {
		display: none;
	}

	&__button {
		display: flex; 
		align-items: center;
		justify-content: center;
	  	margin-right: 10px;
		width: 35px;
	  	height: 35px;
	  	background-color: $color-primary;
		border-radius: 50%;

		font-family: $font-main-regular;
		font-style: normal;
		font-weight: 500;
		font-size: 14px;
		line-height: 19px;
		text-align: center;
		letter-spacing: 2.35846px;
		text-transform: uppercase;
		color: $white;
		text-decoration: none;
		transition: all .5s ease-out;

		&:hover {
			box-shadow: 0px 11px 24px rgba(238, 67, 67, 0.25);
		}
	
		&:active {
			background-color: $color-primary-extra;
			color: rgba($color: $white, $alpha: .55);
		}
	
		&:disabled {
			opacity: .55;
		}
	
		&:focus {
			background: $color-primary-extra;
			box-shadow: 0px 11px 24px rgba(238, 67, 67, 0.25);
		}

		&--disabled {
			opacity: 0.5;
			pointer-events: none;
			cursor: default; 
			&:hover {
				box-shadow: none;
				pointer-events: none;
				cursor: default; 
			}
		}
	}
}

@media (min-width: $desktop) {
	.paginate {
		text-align: left;
		width: 100%;
  
		&__item {
			width: 50px;
			height: 50px;
			font-family: $font-main-regular;
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 19px;
			text-align: center;
			letter-spacing: 2.35846px;
			text-transform: uppercase;
		}

		&__icon {
			&--left {
				margin-right: 16px;
			}
			&--right {
				margin-left: 16px;
			}
		}
  
		&__decor {
				display: block;
				&--left {
					margin-right: 25px;
				}
				&--right {
					margin-left: 25px;
				}
		}
	
		&__text {
				display: block;
		}
  
		&__button {
				display: flex; 
				align-items: center;
				justify-content: center;
				width: 170px;
				height: 50px;
				background-color: $color-primary;
				border-radius: 25px;
				font-weight: 500;
				font-size: 14px;
				line-height: 19px;
				text-align: center;
				letter-spacing: 2.35846px;
		}
  	}
}
/* END PAGINATE */